import * as http from './http';

export function downloadFile(url, body = null, filename) {
    //(body ? http.post(url, body) : http.get(url, false))
    return makeRequest(url, body)
        .then(response => {
            //const arrayBuffer = base64ToArrayBuffer(base64Pdf);
            createAndDownloadBlobFile(response, filename);
        });
}

function makeRequest(url, body = null) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.open(body ? 'POST' : 'GET', url, true);
        xhr.responseType = 'blob';

        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) {
                return;
            }
            if (xhr.status != 200) {
                reject();
                //TODO: add processing of errors
            } else {
                resolve(xhr.response);
            }
        };

        if (body) {
            xhr.setRequestHeader("Content-Type", "application/json");
            xhr.send(JSON.stringify(body));
        } else {
            xhr.send();
        }
    });
}

function base64ToArrayBuffer(base64) {
    const binaryString = window.atob(base64); // Comment this if not using base64
    const bytes = new Uint8Array(binaryString.length);
    return bytes.map((byte, i) => binaryString.charCodeAt(i));
}

function createAndDownloadBlobFile(body, filename, extension = 'pdf') {
    const blob = new Blob([body], {type: 'application/pdf'});
    const fileName = `${filename}.${extension}`;
    if (navigator.msSaveBlob) {
        // IE 10+
        navigator.msSaveBlob(blob, fileName);
    } else {
        const link = document.createElement('a');
        // Browsers that support HTML5 download attribute
        if (link.download !== undefined) {
            const url = URL.createObjectURL(blob);
            link.setAttribute('href', url);
            link.setAttribute('download', fileName);
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
}