<template>
    <div class="adv-gallery__cart__coupon">
        <img
                class="adv-gallery__cart__coupon__image"
                v-bind:src="coupon.imageLink"
        >
        <div class="adv-gallery__cart__coupon__info">
            <div class="adv-gallery__coupon-title">{{title}}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "cart-list-item",
        props: {
            coupon: {
                type: Object,
            }
        },
        computed: {
            title() {
                return this.coupon.productTitles ? this.coupon.productTitles[0] : this.coupon.promotionTitle;
            }
        }
    }
</script>

