<template>
    <button class="adv-btn" v-bind:class="btnCls" v-on:click="onClick">
        <slot name="icon"></slot>
        <slot name="name"></slot>
    </button>
</template>

<script>
    export default {
        name: "btn",
        props: {
            type: {
                type: String,
                default: 'primary',
                validator(value) {
                    return !!['primary', 'secondary', 'primary_outline', 'secondary_outline'].includes(value);
                }
            },
            size: {
                type: String,
                default: 'md',
                validator (value) {
                    return !!['sm', 'md', 'lg'].includes(value);
                }
            }
        },
        computed: {
            btnCls() {
                return [
                    'adv-btn_' + this.type,
                    {
                        'adv-btn_small': this.size === 'sm',
                        'adv-btn_icon': !this.$slots['name']
                    }
                ];
            }
        },
        methods: {
            onClick($event) {
                this.$emit('click', $event);
            }
        }
    }
</script>

<style>
    .adv-btn {
        display: inline-block;
        text-align: center;
        user-select: none;
        background-color: transparent;
        font-size: 0.8rem;
        line-height: 0.8rem;
        padding: 5px 9px;
        border-radius: 3px;
        cursor: pointer;
    }

    .adv-btn * {
        vertical-align: middle;
    }

    .adv-btn .adv-btn__icon {
        width: 22px;
        height: 22px;
        padding-right: 5px;
    }

    .adv-btn.adv-btn_small .adv-btn__icon {
        width: 18px;
        height: 18px;
    }

    .adv-btn_icon .adv-btn__icon {
        padding-right: 0;
    }

    .adv-btn_small {
        border-radius: 2px;
        padding: 5px 5px;
    }

    .adv-btn_primary {
        color: white;
        background: rgb(11, 177, 67);
        border: rgb(11, 177, 67) 1px solid;
    }

    .adv-btn_primary_outline {
        color: rgb(11, 177, 67);
        border: rgb(11, 177, 67) 1px solid;
        background: white;
    }

    .adv-btn_primary .adv-btn__icon,
    .adv-btn_secondary .adv-btn__icon {
        fill: white;
    }

    .adv-btn_primary_outline .adv-btn__icon {
        fill: rgb(11, 177, 67);
    }

    .adv-btn_secondary {
        color: white;
        background: #999999;
        border: 1px solid #999999;
    }

    .adv-btn_secondary_outline {
        color: white;
        background: white;
    }

    .adv-btn_secondary_outline .adv-btn__icon *:not([fill=none]) {
        fill: #999999;
        border: #999999 1px solid;
        border-radius: 3px;
    }
</style>
